import React, { Component } from 'react';
import './Timer.css';
import * as moment from 'moment';

class Timer extends Component {
    constructor(props) {
        super(props);

        this.state = {
            months: 0,
            days: 0,
            hours: 0,
            minutes: 0,
            seconds: 0
        };

        setInterval(this.updateTimer, 1000);
    }

    updateTimer = () => {
        let now = moment();
        let later = moment(this.props.target, "YYYY-MM-DD HH:mm");
        let seconds = Math.floor(later.diff(now, "seconds", true));
        let minutes = Math.floor(seconds / 60);
        let hours = Math.floor(minutes / 60);
        let days = Math.floor(hours / 24);
    
        hours %= 24;
        minutes %= 60;
        seconds %= 60;

        this.setState({
            days: days,
            hours: hours,
            minutes: minutes,
            seconds: seconds
        });
    }

    render() {
        return (
            <div className="timer">
                <div className="timer-element-wrapper days">
                    <span className="timer-element-amount days-left">{this.state.days}</span>
                    <span className="timer-element-text days-text">days</span>
                </div>
                <div className="timer-element-wrapper hours">
                    <span className="timer-element-amount hours-left">{this.state.hours}</span>
                    <span className="timer-element-text hours-text">hours</span>
                </div>
                <div className="timer-element-wrapper minutes">
                    <span className="timer-element-amount minutes-left">{this.state.minutes}</span>
                    <span className="timer-element-text minutes-text">minutes</span>
                </div>
                <div className="timer-element-wrapper seconds">
                    <span className="timer-element-amount seconds-left">{this.state.seconds}</span>
                    <span className="timer-element-text seconds-text">seconds</span>
                </div>
            </div>
        );
    }
}

export default Timer;